.freelance {
  display: none;
}

@include for-tablet-portrait-up {

  .freelance {
    width: 100%;
    display: flex;
    position: relative;
    z-index: 30;
    margin: -2vw 0;
    opacity: 0; // for visible animation

    .content {
      position: relative;
      z-index: 50;
      width: 100%;
      height: 100%;
      padding: 4vw 1.5vw;

      h2 {
        font-size: 2.5vw;
        font-weight: 400;
        width: 100%;
        color: $white;
        text-align: center;
        margin: 0 auto;

        a.button {
          margin: 0 .5em;
        }
      }
    }

    &.visible{
      animation: ease-out fromTop .5s both;
    }

    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: $cyan;
      z-index: 40;
      box-shadow: 0 5px 15px 0 rgba($violet, 0.1);
      transform: skewY(-2deg);
    }
  }
}

@include for-tablet-landscape-up {

  .freelance {

    .content {

      h2 {
        font-size: 2vw;
      }
    }
  }
}
